import { Box, InputAdornment, TextField, useTheme } from "@rp/react/web/mui/core"
import { ArrowBack, Close } from "@rp/react/web/mui/icons"

type HubHeaderInputProps = {
  placeholder: string
  filterTerm: string
  onChange: (value: string) => void
  onClose: (value: boolean) => void
}
const HubFilterInput = ({ placeholder, filterTerm, onChange, onClose }: HubHeaderInputProps) => {
  const theme = useTheme()
  const handleClose = () => {
    onChange("")
    onClose(false)
  }

  return (
    <Box
      sx={{
        backgroundColor: "surface.main",
        position: "absolute",
        width: "100%",
        top: 0,
        left: 0,
        zIndex: 10,
      }}
    >
      <TextField
        sx={{ width: "100%", background: theme.palette.elevation.dp12, borderRadius: 0 }}
        autoFocus
        data-testid={"text-field-mobile"}
        autoComplete="off"
        placeholder={placeholder}
        value={filterTerm}
        onChange={e => onChange(e.target.value)}
        variant="standard"
        InputProps={{
          sx: { height: "56px" },
          disableUnderline: true,
          startAdornment: (
            <InputAdornment sx={{ pr: "6px", pl: 0.5 }} position="start" onClick={handleClose}>
              <ArrowBack />
            </InputAdornment>
          ),
          endAdornment: (
            <InputAdornment
              sx={{ pr: "6px", display: filterTerm ? "" : "none" }}
              position="end"
              onClick={() => onChange("")}
            >
              <Close />
            </InputAdornment>
          ),
        }}
      />
    </Box>
  )
}

export { HubFilterInput }
